#Artist{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: calc(100vw - 16vw);
}

#info{
    height:300px;
    background-color: rgba(0, 0, 0, 0.7);
    margin-top: 30px;
    display: flex;
    width:95%;
    border-radius: 20px;
}

#container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    height:100%;
    margin-left: 30px;
}

#singer{
    width:250px;
    height: 250px;
    border-radius: 100%;
    object-fit: cover;
    box-shadow: 0 0 15px rgba(255, 255, 255,0.5);
}

#details{
    display: flex;
    flex-direction: column;
    margin-left: 50px;
    justify-content: center;
    margin-bottom: 20px;
}

#Name{
    color:white;
    font-size: 60px;
    font-weight: 600;
}

#song_info{
    display:flex;
    color:rgba(255, 255, 255,0.7);
    font-size: 20px;
    margin-top: 5px;
}

#play_all{
    height:40px;
    width:120px;
    border: 2px solid white;
    background-color: rgb(33, 162, 33);
    display: flex;
    font-size: 20px;
    border-radius: 20px;
    justify-content: center;
    color:white;
    align-items: center;
    margin-top: 20px;
    cursor: pointer;
}

#playlist{
    height:300px;
    background-color: rgba(0, 0, 0, 0.7);
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width:95%;
    border-radius: 20px;
}


::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 3px;
}

#title{
    display: flex;
    height:71px;
    width: 92%;
    align-items: center;
    font-size: 20px;
    padding:10px;
    border-bottom: 3px solid white;
    margin-right: 35px;
}

#blank{
    padding-left: 5rem;
    width: 20%;
}

#title_name{
    width: 30%;
    color:white;
}

#title_duration{
    width: 20%;
    color:white;
}

#title_likes{
    width:20%;
}

#songs{
    display: flex;
    height:75px;
    font-size: 15px;
    width:98%;
    align-items: center;
    cursor: pointer;
    border-bottom: 2px solid rgba(255, 255, 255,0.5);
    background-color: none;
    transition: background-color 0.2s;
}

#songs:hover{
    background-color: rgba(255,255,255,0.2);
}


#body{
    overflow-x: scroll;
    scroll-behavior: smooth;
    padding-bottom:10px;
}

#body_name{
    width: 30%;
    color:white;
    opacity: 75%;
}

#body_duration{
    width: 20%;
    color:white;
    opacity: 75%;
}